<template>
    <nav class="navbar navbar-vertical fixed-left navbar-expand-md navbar-light bg-white" id="sidenav-main">
        <div class="container-fluid">

            <!--Toggler-->
            <navbar-toggle-button @click.native="showSidebar">

            </navbar-toggle-button>
            <router-link class="navbar-brand" to="/">
                <img :src="logo" class="navbar-brand-img" alt="...">
            </router-link>

            <slot name="mobile-right">
                <ul class="nav align-items-center d-md-none">

                    <base-dropdown class="nav-item" menu-on-right tag="li" title-tag="a">
                        <a slot="title-container" class="nav-link" href="#" role="button">
                            <div class="media align-items-center">
                              <span class="avatar avatar-sm rounded-circle">
                                <b-icon icon="person-circle"></b-icon>
                              </span>
                            </div>
                        </a>

                        <div class=" dropdown-header noti-title">
                            <h6 class="text-overflow m-0">{{ currentDateFormatted }}</h6>
                        </div>
                        <div  class="dropdown-item">
                            <i class="ni ni-single-02"></i>
                            <span></span>
                        </div>
                        <div  class="dropdown-item">
                            <i class="ni ni-settings-gear-65"></i>
                            <span></span>
                        </div>

                        <div class="dropdown-divider"></div>
                        <a @click="toLoginPage()" class="dropdown-item">
                            <i class="ni ni-user-run"></i>
                            <span></span>
                        </a>
                    </base-dropdown>
                </ul>
            </slot>
            <slot></slot>
            <div v-show="$sidebar.showSidebar" class="navbar-collapse collapse show" id="sidenav-collapse-main">

                <div class="navbar-collapse-header d-md-none">
                    <div class="row">
                        <div class="col-6 collapse-brand">
                            <router-link to="/">
                                <img :src="logo">
                            </router-link>
                        </div>
                        <div class="col-6 collapse-close">
                            <navbar-toggle-button @click.native="closeSidebar"></navbar-toggle-button>
                        </div>
                    </div>
                </div>

                <ul class="navbar-nav">
                    <slot name="links">
                    </slot>
                </ul>
                <!--Divider-->
                <hr class="my-3">
                <!--Heading-->

            </div>
            </div>
    </nav>
</template>
<script>
  import NavbarToggleButton from '@/components/NavbarToggleButton'

  export default {
    name: 'sidebar',
    components: {
      NavbarToggleButton
    },
    props: {
      logo: {
        type: String,
        default: 'img/brand/shxe.svg',
        description: 'Sidebar app logo'
      },
      autoClose: {
        type: Boolean,
        default: true,
        description: 'Whether sidebar should autoclose on mobile when clicking an item'
      }
    },
    provide() {
      return {
        autoClose: this.autoClose
      };
    },
    computed: {
      currentDateFormatted() {
        const today = new Date();
        const year = String(today.getFullYear());
        let month = String(today.getMonth() + 1); // Months are zero-based (0-11)
        if(month.length === 1) {
          month = '0' + month;
        }
        let day = String(today.getDate());
        if(day.length === 1) {
          day = '0' + day;
        }

        return ` ${year}-${month}-${day}`;
      }
    },
    methods: {
      closeSidebar() {
        this.$sidebar.displaySidebar(false)
      },
      showSidebar() {
        this.$sidebar.displaySidebar(true)
      },
      toLoginPage(){
        sessionStorage.removeItem('loginStatus');
        sessionStorage.removeItem('username');
        this.$router.push('/login');  // Navigate using Vue Router
      }
    },
    beforeDestroy() {
      if (this.$sidebar.showSidebar) {
        this.$sidebar.showSidebar = false;
      }
    },


  };
</script>
